/**
 *手机端查看图片控件
 * by:zhuzhongya
 *time:2016年6月28日 19:08:05
 * */
var showSelectImg=function(imgList,openNum){
    this.imgList=imgList||[];
    this.openNum=openNum;
    this.init();
    this.load();
    this.bind();
};
showSelectImg.prototype = {
    init:function(){
        this.winWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        this.winHeight = document.documentElement.clientHeight || document.body.clientHeight || window.innerHeight;
        this.scroTop = document.documentElement.scrollTop || document.body.scrollTop;
        this.isAndroid = navigator.userAgent.match(/Android ([3-9][._][0-9])/);
        this.isOS = navigator.userAgent.match(/OS ([5-9])_\d[_\d]*/);
        this.imgHtmlArr=[];
        this.isTelPhone=this.isMobile();
    },
    load:function(){
        // var position = this.isOS || this.isAndroid && navigator.userAgent.indexOf("UCBrowser") > 0 ? "fixed" : "absolute";
        // console.log(position);
        // var a = this.isOS || this.isAndroid && navigator.userAgent.indexOf("UCBrowser") > 0 ? "fixed": "absolute";
        this.imgHtmlArr.push('<div class="J-slider" style="position: fixed; margin: auto; overflow: hidden;z-index:99;">');
        // transform: translate(-'+this.openNum*this.winWidth+'px, 0px) translateZ(0px);
        this.imgHtmlArr.push('<div id="J-slider-cont" style="position:relative;top:0;left:0;transform: translate(-'+this.openNum*this.winWidth+'px, 0px) translateZ(0px);width: '+this.imgList.length*this.winWidth+'px;height:'+this.winHeight+'px">');
        for(var i= 0,l=this.imgList.length;i<l;i++){
            this.imgHtmlArr.push('<div class="J-wrapper" style="line-height: '+this.winHeight+'px;width: '+this.winWidth+'px;"><img class="J-img" src="'+this.imgList[i]+'" /></div>');
        }

        this.imgHtmlArr.push('</div>');
        this.imgHtmlArr.push('<div class="ctrlbar" style="position:fixed;"><a id="J-close" href="javascript:;">关闭</a><span id="J-curPage">'+(this.openNum+1)+'</span><span id="J-pageCount">/'+this.imgList.length+'</span></div>');
        this.imgHtmlArr.push('</div>');

        // $("body").append(this.imgHtmlArr.join(""));
        // 新建一个div元素节点
        var div = document.createElement("div");
        div.innerHTML = this.imgHtmlArr.join("");
        // 把div元素节点添加到body元素节点中成为其子节点，但是放在body的现有子节点的最后
        document.body.appendChild(div);

        this.b=document.getElementById("J-slider-cont");
        document.body.style.overflow='hidden';
        this.changeTransform(-this.openNum*this.winWidth,0,0);
        if (navigator.userAgent.toLowerCase().indexOf("firefox") > 0) {
            document.querySelector(".ctrlbar").style.cssText = "position:absolute;top:20px;left:0;";
        }
        setTimeout(function() {
            window.scrollTo(0, 1);
        }, 10);
    },
    bind:function(){
        var t=this;
        //isMobile
        this.touchstart= this.isTelPhone?"touchstart":"mousedown";
        this.touchmove=this.isTelPhone?"touchmove":"mousemove";
        this.touchend=this.isTelPhone?"touchend":"mouseup";

        /*$("#J-close").on("click",function(){
            setTimeout(function() {
                window.scrollTo(0, t.scroTop);
                $("#J-slider-cont").parent().remove();
                document.body.style.overflow='auto';
            }, 0);

            /!*$("#J-slider-cont").parent().remove();
            document.body.style.overflow='auto';*!/
        });*/
        document.getElementById("J-close").onclick=function(){
          setTimeout(function() {
            window.scrollTo(0, t.scroTop);
            // $("#J-slider-cont").parent().remove();
            var thisNode=document.getElementById("J-slider-cont").parentNode;
            thisNode.parentNode.removeChild(thisNode);
            document.body.style.overflow='auto';
          }, 0);
        }
        var startX, startY;
        this.b.addEventListener(t.touchstart,function (ev) {
            startX = t.isTelPhone?ev.touches[0].pageX:ev.clientX;
            startY = t.isTelPhone?ev.touches[0].pageY:ev.clientY;
            ev.preventDefault();
        }, false);
        this.b.addEventListener(t.touchend,function (ev) {
            var endX, endY;
            endX = t.isTelPhone?ev.changedTouches[0].pageX:ev.clientX;
            endY = t.isTelPhone?ev.changedTouches[0].pageY:ev.clientY;
            var direction = t.GetSlideDirection(startX, startY, endX, endY);
            switch(direction) {
                case 0:
                    //没滑动
                    break;
                case 1:
                    //向上
                    break;
                case 2:
                    //向下
                    break;
                case 3:
                    //向左
                    if(t.openNum>=t.imgList.length-1){return false;}
                    t.openNum++;
                    t.startMove();
                    //alert("!");
                    break;
                case 4:
                    //向右
                    if(t.openNum<1){return false;}
                    t.openNum--;
                    t.startMove();
                    break;
                default:
            }
            ev.preventDefault();
        }, false);
    },
    isMobile:function(){
        var sUserAgent= navigator.userAgent.toLowerCase(),
            bIsIpad= sUserAgent.match(/ipad/i) == "ipad",
            bIsIphoneOs= sUserAgent.match(/iphone os/i) == "iphone os",
            bIsMidp= sUserAgent.match(/midp/i) == "midp",
            bIsUc7= sUserAgent.match(/rv:1.2.3.4/i) == "rv:1.2.3.4",
            bIsUc= sUserAgent.match(/ucweb/i) == "ucweb",
            bIsAndroid= sUserAgent.match(/android/i) == "android",
            bIsCE= sUserAgent.match(/windows ce/i) == "windows ce",
            bIsWM= sUserAgent.match(/windows mobile/i) == "windows mobile",
            bIsWebview = sUserAgent.match(/webview/i) == "webview";
        return (bIsIpad || bIsIphoneOs || bIsMidp || bIsUc7 || bIsUc || bIsAndroid || bIsCE || bIsWM);
    },
    startMove:function(){
        var t=this;
        /*console.log(t.openNum,this.imgList.length-1);
        if(t.openNum<0){return false;}
        if(t.openNum>=this.imgList.length-1){return false;}*/
        // $("#J-curPage").html(t.openNum+1);
        document.getElementById("J-curPage").innerHTML = t.openNum+1;
        t.changeTransition("all", .3, "ease-out");
        t.changeTransform(-t.openNum*t.winWidth,0,0);
    },
    //返回角度
    GetSlideAngle:function(dx, dy){
        return Math.atan2(dy, dx) * 180 / Math.PI;
    },
    //根据起点和终点返回方向 1：向上，2：向下，3：向左，4：向右,0：未滑动
    GetSlideDirection:function(startX, startY, endX, endY){
        var dy = startY - endY;
        var dx = endX - startX;
        var result = 0;

        //如果滑动距离太短
        if(Math.abs(dx) < 2 && Math.abs(dy) < 2) {
            return result;
        }

        var angle = this.GetSlideAngle(dx, dy);
        if(angle >= -45 && angle < 45) {
            result = 4;
        }else if (angle >= 45 && angle < 135) {
            result = 1;
        }else if (angle >= -135 && angle < -45) {
            result = 2;
        }
        else if ((angle >= 135 && angle <= 180) || (angle >= -180 && angle < -135)) {
            result = 3;
        }

        return result;
    },
    changeTransform:function(a, c, d){
        this.b.style.transform = "translate(" + a + "px," + c + "px) translateZ(0)";
        this.b.style.msTransform = "translate(" + a + "px," + c + "px) translateZ(0)";/* IE 9 */
        this.b.style.MozTransform = "translate(" + a + "px," + c + "px) translateZ(0)";/* Firefox */
        this.b.style.webkitTransform = "translate(" + a + "px," + c + "px) translateZ(0)";/* Safari 和 Chrome */
        this.b.style.oTransform = "translate(" + a + "px," + c + "px) translateZ(0)";/* Opera */
    },
    changeTransition:function(a, c, d){
        this.b.style.transition = a + " " + c + "s " + d;
        this.b.style.msTransition = a + " " + c + "s " + d; /* IE 9 */
        this.b.style.MozTransition = a + " " + c + "s " + d;/* Firefox */
        this.b.style.webkitTransition = a + " " + c + "s " + d;/* Safari 和 Chrome */
        this.b.style.oTransition = a + " " + c + "s " + d;/* Opera */
    }
};

export default showSelectImg



