import Bscroll from 'better-scroll'

export default {
  name: 'detailsimg',
  data: function () {
    return {
      currentPageIndex: 0,
      dots: [],
      nodeLength: 0
    }
  },
  props: {
    loop: {
      type: Boolean,
      default: true
    },
    autoPlay: {
      type: Boolean,
      default: true
    },
    interval: {
      type: Number,
      default: 4000
    },
    dotShow: {
      type: Boolean,
      default: false
    }
  },
  mounted () {
    this.$nextTick(() => {
      setTimeout(() => {
        this.initTabScroll()
        this.initDots()
        this.initSlider()
        if (this.scroll && this.autoPlay) {
          clearTimeout(this.timer)
          this.autoMove()
        }
      }, 20)
      window.addEventListener('resize', () => {
        if (!this.scroll) {
          return
        }
        setTimeout(() => {
          this.initTabScroll(true)
          this.scroll.refresh()
        }, 20)
      })
    })
  },
  activated() {
    if (this.scroll && this.autoPlay) {
      clearTimeout(this.timer)
      this.autoMove()
    }
    // this.autoMove() // keep-alive 组件激活时调用
  },
  deactivated() {
    clearTimeout(this.timer) // keep-alive 组件停用时调用
  },
  beforeDestroy() {
    clearTimeout(this.timer) // 实例销毁之前调用。在这一步，实例仍然完全可用。
  },
  destroyed() {
    clearTimeout(this.timer)
  },
  methods: {
    initTabScroll(isResize) {
      let width = 0
      let oneWidth = 0
      this.children = this.$refs.tabWrapper.children
      this.nodeLength = this.children.length
      oneWidth = this.$refs.wrapper.getBoundingClientRect().width
      for (let i = 0; i < this.children.length; i++) {
        let child = this.children[i]
        child.style.width = oneWidth + 'px'
        width += oneWidth
      }
      console.log(oneWidth, width)
      if (this.loop && !isResize && this.children.length > 1) {
        width += 2 * oneWidth
      }
      this.$refs.tabWrapper.style.width = width + 'px'
    },
    initSlider() {
      if (!this.scroll) {
        this.scroll = new Bscroll(this.$refs.wrapper, {
          click: true,
          scrollX: true,
          momentum: false,
          'eventPassthrough': 'vertical', // 横向滚动后会默认阻止纵向滚动，开启
          snap: {
            loop: this.loop,
            threshold: 0.1,
            speed: 400
          }
        })

        this.scroll.on('scrollEnd', () => {
          let pageIndex = this.scroll.getCurrentPage().pageX
          /* if (this.loop) {
            pageIndex -= 1
          } */
          this.currentPageIndex = pageIndex
          if (this.autoPlay) {
            clearTimeout(this.timer)
            this.autoMove()
          }
        })
        this.scroll.on('beforeScrollStart', () => {
          if (this.autoPlay) {
            clearTimeout(this.timer)
          }
        })
      } else {
        this.scroll.refresh()
      }
    },
    autoMove() {
      let pageIndex = this.currentPageIndex + 1
      if (pageIndex === (this.loop ? this.children.length - 2 : this.children.length)) {
        pageIndex = 0
      }
      this.timer = setTimeout(() => {
        this.scroll.goToPage(pageIndex, 0, 400)
      }, this.interval)
    },
    initDots() {
      if (this.dotShow) {
        this.dots = new Array(this.children.length)
      }
    }
  }
}



