import BScroll from 'better-scroll'
import detailsimg from '@/base/detailsimg/detailsimg'
import showSelectImg from '@/common/showImgDetail.js'
import tools from '@/common/tools.js'
import axios from 'axios'

export default {
  name: 'equipdetails',
  data: function() {
    return {
      canshupopShow: false,
      gwqpopShow: false,
      shopcartpopShow: false,
      itemId: 0,
      allData: {},
      imgArr: [],
      guigeData: {},
      guigeString0: '',
      guigeString1: '',
      guigeString2: '',
      guigeString3: '',
      spanNum: 0,
      guige0: 0,
      guige1: 0,
      guige2: 0,
      guige3: 0,
      shopCartNowPrice: 0,
      shopCartKuCun: 0,
      shopCartId: 0,
      buyNum: 1,
      isDisabled: true,
      isBuyNowStatus: false,
      activity_title: '',
      getAjaxPriceNum: null
    }
  },
  created() {
    this.itemId = this.$route.query.id
    console.log(this.itemId)
    this._getEquipData()
    this._getGuiGeData()
  },
  deactivated () {
    this.$destroy()
  },
  mounted() {

  },
  watch: {
    '$route' (to, from) { // 监听路由是否变化
      if (to.name === 'equipdetails') {
        console.log('路由监听到了')
        this.itemId = this.$route.query.id
        this.imgArr = []
        this.guigeData = {}
        this.canshupopShow = false
        this.gwqpopShow = false
        this.shopcartpopShow = false

        this.guigeString0 = ''
        this.guigeString1 =  ''
        this.guigeString2 =  ''
        this.guigeString3 =  ''
        this.spanNum =  0
        this.guige0 =  0
        this.guige1 =  0
        this.guige2 =  0
        this.guige3 =  0
        this.shopCartNowPrice =  0
        this.shopCartKuCun =  0
        this.shopCartId =  0
        this.buyNum =  1
        this.isDisabled =  true

        // this.baoyou = this.$route.query.post
        if (!!this.itemId) {
          this._getEquipData()
          this._getGuiGeData()
        }
        /* if (this.bigId !== parseInt(this.$route.query.id) && (this.$route.query.id || this.$route.query.id === 0)) {// 判断条件1  判断传递值的变化
          this.bigId = parseInt(this.$route.query.id)
          this.categoryId = 0
          this._initCanshu()
          this._getData()
        } */
      }
    }
  },
  methods: {
    pxToRem(_s) {
      // 匹配:20px或: 20px不区分大小写
      var reg = /(\:|: )+(\d)+(px)/gi
      let newStr= _s.replace(reg, function(_x) {
        _x = _x.replace(/(\:|: )/, '').replace(/px/i, '')
        return ':' + parseFloat(_x) / 100 + 'rem'
      })
      return newStr
    },
    buyNow() {
      this.isBuyNowStatus = true
      if (this.guigeData.goods_spec) {
        this.shopcartpopShow = true
        this.countPrice()
        setTimeout(() => {
          this._initShopCartScroll()
        }, 20)
      } else {
        this.shopcartPopSure()
      }
    },
    shopcartPopSure() {
      if (this.isBuyNowStatus) {
        this.$router.push({path: 'confirmorder', query: { goods_id: this.itemId, goods_num: this.buyNum, item_id: this.shopCartId, action: 'buy_now'}})
      } else {
        let url = '/Mobile/Cart/AddCart.html'
        axios.get(url, {
          params: {
            goods_id: this.itemId,
            goods_num: this.buyNum,
            item_id: this.shopCartId
          }
        })
          .then((res) => {
            console.log(res)
            if (res.status === 200) {
              tools.showError(res.data.msg)
              this.shopcartpopShow = false
            }
          })
          .catch((error) => {
            console.log(error)
          })
      }
    },
    addCount(addOrCut) {
      if (addOrCut === 'add') {
        this.buyNum++
        this.isDisabled = false
      } else {
        this.buyNum--
        if (this.buyNum === 1) {
          this.isDisabled = true
        }
      }
    },
    checkGuige(index, guigeId, num) {
      console.log(index, guigeId)
      // this.spanNum = num
      switch (index) {
        case 0:
          this.guigeString0 = guigeId
          this.guige0 = num
          break
        case 1:
         this.guigeString1 = guigeId
          this.guige1 = num
         break
        case 2:
         this.guigeString2 = guigeId
          this.guige2 = num
         break
        case 3:
          this.guigeString3 = guigeId
          this.guige3 = num
          break
      }

      let allGuiGe = this.guigeString0 + (this.guigeString1 ? ('_' + this.guigeString1) : '') + (this.guigeString2 ? ('_' + this.guigeString2) : '') + (this.guigeString3 ? ('_' + this.guigeString3) : '')
      console.log(allGuiGe)
      console.log(this.guigeData.spec_goods_price[allGuiGe])
      let item = this.guigeData.spec_goods_price[allGuiGe]
      if (item) {
        this.shopCartNowPrice = item.price
        this.shopCartKuCun = item.store_count
        this.shopCartId = item.item_id
      }
      // console.log(this.$refs.spanKey_11)
      // event.currentTarget.className = "on"
      // this.setClass('spanKey_' + guigeId)
    },
    _getGuiGeData() {
      let url = '/Mobile/Goods/goodsAttr.html'
      axios.get(url, {
        params: {
          id: this.itemId
        }
      })
        .then((res) => {
          console.log(res)
          if (res.status === 200) {
            this.guigeData = res.data
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    _getEquipData() {
      let url = '/Mobile/Goods/goodsInfo.html'
      axios.get(url, {
        params: {
          id: this.itemId
        }
      })
        .then((res) => {
          console.log(res)
          if (res.status === 200) {
            if (res.data.code === 401) { // 商品已下架
              tools.showError(res.data.data)
              setTimeout(() => {
                this.$router.go(-1)
              }, 1000)
            } else {
              this.allData = res.data
              this.setImgArr()
              this.$store.commit('LOADING_SHOW', false)
            }
          }
        })
        .catch((error) => {
          console.log(error)
        })

      let url2 = '/Mobile/Goods/activity.html'
      axios.get(url2, {
        params: {
          goods_id: this.itemId
        }
      })
        .then((res) => {
          console.log(res)
          if (res.data.status == 1) { // 商品已下架
            this.activity_title = res.data.result.goods.activity_title
            this.getAjaxPriceNum = res.data.result.goods.shop_price
          } else {
            tools.showError(res.data.msg)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    openGuiGePop() {
      this.canshupopShow = true
      setTimeout(() => {
        this._initGuiGeScroll()
      }, 20)
    },
    openGouWuQuanPop() {
      this.gwqpopShow = true
      setTimeout(() => {
        this._initGouWuQuanScroll()
      }, 20)
    },
    openShopCartPop() {
      if (this.guigeData.goods_spec) {
        this.shopcartpopShow = true
        this.countPrice()
        setTimeout(() => {
          this._initShopCartScroll()
        }, 20)
      } else {
        this.shopcartPopSure()
      }
    },
    countPrice() {
      let priceData = this.guigeData.goods_spec
      // this.
      let num = 0
      let ggArr = []
      for (const mk in priceData) {
        console.log(mk, priceData[mk])
        switch (num) {
          case 0:
            this.guigeString0 = priceData[mk][0]["item_id"]
            ggArr.push(this.guigeString0)
            break
          case 1:
            this.guigeString1 = priceData[mk][0]["item_id"]
            ggArr.push(this.guigeString1)
            break
          case 2:
            this.guigeString2 = priceData[mk][0]["item_id"]
            ggArr.push(this.guigeString2)
            break
          case 3:
            this.guigeString3 = priceData[mk][0]["item_id"]
            ggArr.push(this.guigeString3)
            break
        }
        num++
      }
      console.log(this.guigeString0, this.guigeString1, this.guigeString2, this.guigeString3)
      // let str1 =
      console.log(ggArr.join('_'))
      const allGuiGe = ggArr.join('_')
      let item = this.guigeData.spec_goods_price[allGuiGe]
      if (item) {
        this.shopCartNowPrice = item.price
        this.shopCartKuCun = item.store_count
        this.shopCartId = item.item_id
      }
    },
    _initGuiGeScroll() {
      if (!this.guiGeScroll) {
        this.guiGeScroll = new BScroll(this.$refs.cspopList, {
          scrollY: true,
          startY: 0,
          click: true
        })
      }
    },
    _initGouWuQuanScroll() {
      if (!this.gouWuQuanScroll) {
        this.gouWuQuanScroll = new BScroll(this.$refs.gwqpopList, {
          scrollY: true,
          startY: 0,
          click: true
        })
      }
    },
    _initShopCartScroll() {
      if (!this.shopCartScroll) {
        this.shopCartScroll = new BScroll(this.$refs.shopcartList, {
          scrollY: true,
          startY: 0,
          click: true
        })
      }
    },
    showBigImgPop(num) {
      new showSelectImg(this.imgArr, num);
    },
    setImgArr() {
      let goods_image = this.allData.goods_image
      for (let i = 0; i < goods_image.length; i++) {
        this.imgArr.push(goods_image[i].image_url)
      }
    },
    getYouhuiquan(couponId) {
      let url = '/Mobile/Goods/getCoupon.html'
      axios.get(url, {
        params: {
          coupon_id: couponId
        }
      })
        .then((res) => {
          console.log(res)
          if (res.status === 200) {
            /* if (res.data.status === 1) {
              tools.showError(res.data.msg)
            } */
            tools.showError(res.data.msg)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    goPingjia(itemId) {
      this.$router.push({path: 'assessdetails', query: { itemId: this.itemId}})
    }
  },
  components: {
    detailsimg
  }
}



